.card {
  border-radius: 25px;
  height: 225px;
  width: 250px;
  margin: 10px;
  box-shadow: 0 6px 10px grey, 0 6px 10px grey;
  text-align: left;
  cursor: pointer;
  background: transparent;
}
/* https://github.com/IanLunn/Hover*/
/* Radial Out */
.hvr-radial-out {
  display: inline-block;
  vertical-align: middle;
  -webkit-transform: perspective(1px) translateZ(0);
  transform: perspective(1px) translateZ(0);
  overflow: hidden;
  -webkit-transition-property: color;
  transition-property: color;
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;
}
.hvr-radial-out:before {
  content: "";
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: -webkit-radial-gradient(#5de7ff 0%, #0092ec 100%);
  background: radial-gradient(#5de7ff 0%, #0092ec 100%);
  border-radius: 100%;
  -webkit-transform: scale(0);
  transform: scale(0);
  -webkit-transition-property: transform;
  -webkit-transition-property: -webkit-transform;
  transition-property: -webkit-transform;
  transition-property: transform;
  transition-property: transform, -webkit-transform;
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;
  -webkit-transition-timing-function: ease-out;
  transition-timing-function: ease-out;
}
.hvr-radial-out:hover,
.hvr-radial-out:focus,
.hvr-radial-out:active {
  color: white;
}

.hvr-radial-out:hover:before,
.hvr-radial-out:focus:before,
.hvr-radial-out:active:before {
  -webkit-transform: scale(2);
  transform: scale(2);
}
.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  border-radius: 25px;
}
.card > .img-container > img {
  width: 95%;
}
.card > .img-container > img:hover {
  /* https://www.w3schools.com/howto/howto_css_shake_image.asp */
  /* Start the shake animation and make the animation last for 0.5 seconds */
  -webkit-animation: shake 0.5s;
          animation: shake 0.5s;
  /* When the animation is finished, start again */
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}
@-webkit-keyframes shake {
  0% {
    -webkit-transform: translate(1px, 1px) rotate(0deg);
            transform: translate(1px, 1px) rotate(0deg);
  }
  10% {
    -webkit-transform: translate(-1px, -2px) rotate(-1deg);
            transform: translate(-1px, -2px) rotate(-1deg);
  }
  20% {
    -webkit-transform: translate(-3px, 0px) rotate(1deg);
            transform: translate(-3px, 0px) rotate(1deg);
  }
  30% {
    -webkit-transform: translate(3px, 2px) rotate(0deg);
            transform: translate(3px, 2px) rotate(0deg);
  }
  40% {
    -webkit-transform: translate(1px, -1px) rotate(1deg);
            transform: translate(1px, -1px) rotate(1deg);
  }
  50% {
    -webkit-transform: translate(-1px, 2px) rotate(-1deg);
            transform: translate(-1px, 2px) rotate(-1deg);
  }
  60% {
    -webkit-transform: translate(-3px, 1px) rotate(0deg);
            transform: translate(-3px, 1px) rotate(0deg);
  }
  70% {
    -webkit-transform: translate(3px, 1px) rotate(-1deg);
            transform: translate(3px, 1px) rotate(-1deg);
  }
  80% {
    -webkit-transform: translate(-1px, -1px) rotate(1deg);
            transform: translate(-1px, -1px) rotate(1deg);
  }
  90% {
    -webkit-transform: translate(1px, 2px) rotate(0deg);
            transform: translate(1px, 2px) rotate(0deg);
  }
  100% {
    -webkit-transform: translate(1px, -2px) rotate(-1deg);
            transform: translate(1px, -2px) rotate(-1deg);
  }
}
@keyframes shake {
  0% {
    -webkit-transform: translate(1px, 1px) rotate(0deg);
            transform: translate(1px, 1px) rotate(0deg);
  }
  10% {
    -webkit-transform: translate(-1px, -2px) rotate(-1deg);
            transform: translate(-1px, -2px) rotate(-1deg);
  }
  20% {
    -webkit-transform: translate(-3px, 0px) rotate(1deg);
            transform: translate(-3px, 0px) rotate(1deg);
  }
  30% {
    -webkit-transform: translate(3px, 2px) rotate(0deg);
            transform: translate(3px, 2px) rotate(0deg);
  }
  40% {
    -webkit-transform: translate(1px, -1px) rotate(1deg);
            transform: translate(1px, -1px) rotate(1deg);
  }
  50% {
    -webkit-transform: translate(-1px, 2px) rotate(-1deg);
            transform: translate(-1px, 2px) rotate(-1deg);
  }
  60% {
    -webkit-transform: translate(-3px, 1px) rotate(0deg);
            transform: translate(-3px, 1px) rotate(0deg);
  }
  70% {
    -webkit-transform: translate(3px, 1px) rotate(-1deg);
            transform: translate(3px, 1px) rotate(-1deg);
  }
  80% {
    -webkit-transform: translate(-1px, -1px) rotate(1deg);
            transform: translate(-1px, -1px) rotate(1deg);
  }
  90% {
    -webkit-transform: translate(1px, 2px) rotate(0deg);
            transform: translate(1px, 2px) rotate(0deg);
  }
  100% {
    -webkit-transform: translate(1px, -2px) rotate(-1deg);
            transform: translate(1px, -2px) rotate(-1deg);
  }
}
.card > .content {
  font-size: 22px;
  color: black;
  text-shadow: -1px -1px 1px #aaa, 0px 4px 1px grey, 4px 4px 5px grey,
    0px 0px 7px grey;
  text-align: center;
  background: white;
  opacity: 0.72;
  border-radius: 30px 30px 30px 30px;
}

@media only screen and (min-width: 1920px) {
  .card {
    height: 275px;
    width: 300px;
    margin: 20px;
  }
}

@media only screen and (min-width: 2400px) {
  .card {
    height: 400px;
    width: 425px;
    margin: 20px;
  }
}

@media only screen and (max-width: 1250px) {
  .card {
    margin: 0px;
    height: 200px;
    width: 225px;
  }
}

@media only screen and (max-width: 1023px) {
  .card {
    margin: 0px;
    height: 175px;
    width: 200px;
  }
}

.wrapper {
  height: 100%;
  padding: 25px 75px 0 75px;
  width: 90%;
  margin: auto;
}

@media only screen and (max-width: 1024px) {
  .wrapper {
    padding: 5px 0 0 0;
    width: auto;
  }
}

.board {
  display: grid;
  grid-template-columns: auto auto auto auto;
  -webkit-justify-content: center;
          justify-content: center;
}

@media only screen and (max-width: 1019px) {
  .board {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    padding: 10px;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
  }
}

.titlebox {
  text-align: center;
}

.title {
  font-size: 3.5rem;
  font-style: italic;
  text-decoration: underline;
  color: white;
  text-shadow: -1px -1px 1px #aaa, 0px 4px 1px grey, 4px 4px 5px grey,
    0px 0px 7px grey;
  margin: 0px;
}

@media only screen and (max-width: 1024px) {
  .title {
    font-size: 2rem;
  }
}

@media only screen and (max-width: 425px) {
  .title {
    font-size: 1.6rem;
  }
}

.score,
.message {
  color: white;
  display: block;
  text-align: center;
  font-size: 26px;
  text-shadow: -1px -1px 1px gray, 0px 2px 3px grey, 4px 2px 5px grey,
    2px 0px 7px grey;
  margin-bottom: 2px;
  position: -webkit-sticky;
  position: sticky;
}
.message {
  font-weight: bold;
}
span {
  margin: 0px 10px 0px 25px;
}
.visi {
  visibility: visible;
}

@media only screen and (max-width: 1024px) {
  .score,
  .message {
    font-size: 20px;
  }
  span {
    margin: 0px 5px 0px 12.5px;
  }
  .visi {
    visibility: hidden;
  }
}

.footer {
  background: rgba(0, 0, 0, 0.33);
  border-top: 1px solid #e7e7e7;
  text-align: center;
  padding: 5px;
  position: fixed;
  left: 0;
  bottom: 0;
  height: 100px;
  width: 100%;
  pointer-events: none;
}

.phantom {
  display: block;
  padding: 20px;
  height: 100px;
  width: 100%;
}

* {
  box-sizing: border-box;
  font-family: sans-serif;
}

